<template>
  <div class="demopage no_border longer-padding">
    <el-container>
      <el-header class="title no_border">
        {{ $t('message.demopage.testbed_title')}}
      </el-header>
      <el-main>
        <el-image :src=testbed alt="Testbed Image" class="testbed-image" :preview-src-list=[testbed] ></el-image>
      </el-main>
      <el-aside width="100%" class="testbed_text">{{ $t('message.demopage.testbed_text') }}</el-aside>
    </el-container>
      <el-header class="title">
        {{ $t('message.demopage.demo_title')}}
      </el-header>
    <div class="media-list">
    <DemoItem
      v-for="(item, index) in tasks"
      :key="index"
      :image="item.image"
      :title="$t(item.title)"
      :description="$t(item.description)"
      :video="item.video"
    />
  </div>
  </div>
</template>

<script>
import task1Png from '@/assets/task1.png'
import task1Mp4 from '@/assets/task1.mp4'
import task2Png from '@/assets/task2.png'
import task2Mp4 from '@/assets/task2.mp4'
import testbed from '@/assets/testbed.png'
// import task3Png from '@/assets/task3.png'
import DemoItem from '@/components/DemoItem.vue'
export default {
  name: 'demo',
  components: {
    DemoItem
  },
  data () {
    return {
      testbed: testbed,
      tasks: [
        {
          title: 'message.demopage.tasks.item1.title',
          description: `message.demopage.tasks.item1.description`,
          image: task1Png,
          video: task1Mp4
        },
        {
          title: 'message.demopage.tasks.item2.title',
          description: `message.demopage.tasks.item2.description`,
          image: task2Png,
          video: task2Mp4
        }
      ]
    }
  },
  created () {
    console.log('HomePage一创建')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
  font-size: 30px;
  line-height: 80px;
  font-weight: bold;
  text-align: center;
  height: 100px
}
.no_border{
  border: none;
  box-shadow: none;
}
.longer-padding{
  padding:0 300px
}
.testbed_text{
  text-align: left;
  padding: 20px;
}
</style>



