<template>
    <div class="footer-card">
      <div class="info">
        <h2 class="name">Qunbi Zhuge （诸葛群碧）</h2>
        <p class="position">Deputy Dean</p>
        <p class="department">School of Integrated Circuits</p>
      </div>
      <div class="contact">
        <p>
          <i class="el-icon-location" /> Office: SEIEE Building 5-519
        </p>
        <p>
            <i class="el-icon-message" /> Email: qunbi.zhuge@sjtu.edu.cn
        </p>
      </div>
    </div>
  </template>
<script>
export default {
  name: 'FooterCard'
}
</script>
  <style scoped>
  .footer-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #17385e;
    color: #fff;
    padding: 20px 200px;
    border-radius: 8px;
    font-family: Arial, sans-serif;
  }
  .info {
    flex: 6;
    text-align: left;
  }
  .name {
    font-size: 1.5em;
    margin: 0;
  }
  .position,
  .department {
    margin: 5px 0;
    font-size: 0.9em;
  }
  .contact {
    flex: 4;
    font-size: 0.9em;
    text-align: left;
  }
  .contact p {
    margin: 5px 0;
  }
  .icon {
    margin-right: 5px;
  }
  .language-switcher {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .lang-switch-btn,
  .settings-btn {
    background-color: transparent;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    font-size: 1.2em;
    margin: 5px 0;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
  }
  .lang-switch-btn:hover,
  .settings-btn:hover {
    background-color: #fff;
    color: #17385e;
    transform: scale(1.1);
  }
  </style>



