<template>
  <div class="news-list longer-padding">
    <NewsItem
      v-for="(item, index) in news"
      :key="index"
      :category="$t(item.category)"
      :date="$t(item.date)"
      :title="$t(item.title)"
      :description="$t(item.description)"
      :image="item.image"
      :time="$t(item.time)"
    />
  </div>
</template>

<script>
import NewsItem from './NewsItem.vue'
export default {
  name: 'NewsList',
  components: {
    NewsItem
  },
  data () {
    return {
      news: [
        {
          title: 'message.newspage.news.item1.title',
          time: 'message.newspage.news.item1.time'
        },
        {
          title: 'message.newspage.news.item2.title',
          time: 'message.newspage.news.item2.time'
        },
        {
          title: 'message.newspage.news.item3.title',
          time: 'message.newspage.news.item3.time'
        },
        {
          title: 'message.newspage.news.item4.title',
          time: 'message.newspage.news.item4.time'
        }
      ]
    }
  }
}
</script>

<style scoped>
.news-list {
  max-width: 100%;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
}
.longer-padding{
  padding:0 300px
}
</style>



